@import "helpers/mixins";

h2:not([data-text="welcome"]) {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 2rem;
  position: relative;

  &:not(:first-of-type) {
    margin-top: 3rem;
  }

  &::before {
    content: "";
    background: {
      image: url("../assets/images/channels/newsHbg.webp");
      size: contain;
      repeat: no-repeat;
    }
    position: absolute;
    height: 100px;
    width: 100px;
    top: 50%;
    z-index: -1;
    transform: translate(-20px, -44%);
  }
}

.swiper.narrowSwiper.swiper2 {
  .swiper-wrapper {
    .swiper-slide-active {
      img {
        // transform: scale(1.2);
      }
    }
    img {
      transition: 0.4s;
      border-bottom-right-radius: 1rem;
      height: 550px;
      object-fit: cover;
      max-width: unset;
    }
  }
}
.sweeper .swiper {
  padding-bottom: 30rem;
}

.box {
  &:not(:last-child) {
    margin-bottom: 5rem;
  }

  .episodes {
    position: relative;
    display: grid;
    & > * {
      justify-self: center;
    }
    grid-template-columns: repeat(
      auto-fill,
      minmax(25%, 1fr)
    );

    gap: 5rem;
    .ep {
      padding-bottom: 1.5rem;
      position: relative;

      .imgCover {
        img:nth-child(2) {
          position: absolute;
          left: -3px;
          bottom: 0;
          max-width: 96%;
          z-index: -1;
        }
      }

      p {
        padding-left: 0.5rem;
        font-size: 0.8rem;
      }

      .btns {
        bottom: unset;
        transform: translate(30px, 4px);

        .specBtn {
          background-color: #cf1319;
          color: white;
        }
      }
      img {
        max-height: 25vh;
      }
    }
  }

  .teasers {
    display: grid;

    .tea {
      @include breakpoints(mobile) {
        flex-direction: column;
        margin-bottom: 3.5rem;
      }
      @include breakpoints(min-xlarge) {
        gap: 1rem;
      }
      img {
        max-width: 180px;
      }

      &:nth-child(even) {
        place-self: end;
        direction: rtl;
      }

      @include breakpoints(min-xlarge) {
        width: 50%;

        .txt {
          position: relative;
          padding-right: 1rem;

          @include breakpoints(min-large) {
            width: 40%;
          }

          &::before {
            content: "";
            position: absolute;
            height: 100%;
            width: 100%;
            padding: 10rem 22rem;
            top: 8%;
            left: 86%;
            transform: translate(-44%, -38%);
            z-index: -1;
            @media (min-width: 1800px) {
              transform: translate(-40%, -40%) scale(1.2);
            }

            background: {
              image: url("../assets/images/shows/typing area-01.webp");
              size: contain;
              repeat: no-repeat;
            }
          }

          &.t2::before {
            transform: translate(-72%, -8%) rotate(20deg)
              scale(1.2);
            padding: 14rem;

            @media (min-width: 1800px) {
              transform: translate(-74%, -17%) rotate(20deg)
                scale(1.2);
            }
          }

          &.r1::before {
            transform: translate(-89%, -35%) rotate(133deg)
              scale(1.3);
            padding-right: 0;

            @media (min-width: 1800px) {
              transform: translate(-92%, -30%)
                rotate(133deg) scale(1.3);
            }
          }

          &.l1::before {
            transform: translate(-68%, -25%) rotate(-55deg)
              scale(1.5);
            padding: 10rem;

            @media (min-width: 1800px) {
              transform: translate(-61%, 10%) rotate(-36deg)
                scale(2);
            }
          }

          h3 {
            font-weight: bold;
            margin-bottom: 1.1rem;
            font-size: 1.5rem;
          }
          p {
            font-size: 1.3rem;
          }
        }
      }

      .shelt-imgs {
        display: flex;
        width: 180px;
        height: 250px;
        position: relative;

        img {
          position: absolute;
          top: 0;

          &:nth-child(1) {
            z-index: 4;
          }

          &:nth-child(2) {
            left: 15%;

            @include breakpoints(max-xlarge) {
              left: 5px;
            }

            z-index: 3;
          }
          &:nth-child(3) {
            left: 30%;

            @include breakpoints(max-xlarge) {
              position: relative;
              left: 10px;
            }

            z-index: 2;
          }
        }
      }
    }
  }
}

.btns.pp {
  top: 117%;
  font-size: 0.9rem;
  height: fit-content;
  position: relative;

  p {
    color: red;
  }
}

.bg1 {
  direction: rtl;
  img {
    position: absolute;
    max-width: 130px;
    transform: translateX(-6rem) rotate(35deg);
  }
}
.bg2 {
  img {
    max-width: 130px;
    position: absolute;
    left: 0;
    transform: translateY(-12rem) scaleX(-1);
    z-index: -99;

    @include breakpoints(mobile) {
      opacity: 0.7;
    }
  }
}
body.arabic {
  .bg1 {
    direction: ltr;
    img {
      transform: translateX(6rem) rotate(35deg);
    }
  }

  .bg2 {
    img {
      right: 0;
      left: unset;
      transform: translateY(-12rem);
    }
  }

  h2:not([data-text="welcome"]) {
    text-align: right;
    &::before {
      right: 0;
    }
  }
}

.khaltbeta-page {
  .bg1 {
    img {
      left: 0;
      max-width: 20vw;
    }
  }

  .contact {
    &::before,
    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: 0;
    }

    &::before {
      aspect-ratio: 1;
      width: 45vw;
      z-index: -1;
      transform: translateY(4rem) scale(-1);

      @include breakpoints(mobile) {
        transform: translateY(5rem) scale(-1);
      }

      background: {
        image: url("../assets/images/all_pages/newBgs4.webp");
        size: contain;
        repeat: no-repeat;
      }
    }
    &::after {
      width: calc(min(15vw, 100px));
      aspect-ratio: 0.5;
      transform: translateY(4rem);
      z-index: -1;

      @include breakpoints(mobile) {
        transform: translateY(6rem);
      }

      background: {
        image: url("../assets/images/all_pages/newBgs3.webp");
        size: contain;
        repeat: no-repeat;
      }
    }
  }
}

