// Gird system >> Media Queries Mixin
@mixin breakpoints($point) {
  @if $point == "tinny-mobile" {
    @media (max-width: 425px) {
      @content;
    }
  } @else if $point == "mobile" {
    @media (max-width: 576px) {
      @content;
    }
  } @else if $point == "min-small" {
    @media (min-width: 577px) {
      @content;
    }
  } @else if $point == "small" {
    @media (min-width: 577px) and (max-width: 767px) {
      @content;
    }
  } @else if $point == "max-small" {
    @media (max-width: 767px) {
      @content;
    }
  } @else if $point == "min-medium" {
    @media (min-width: 768px) {
      @content;
    }
  } @else if $point == "medium" {
    @media (min-width: 768px) and (max-width: 991px) {
      @content;
    }
  } @else if $point == "max-medium" {
    @media (max-width: 991px) {
      @content;
    }
  } @else if $point == "min-large" {
    @media (min-width: 992px) {
      @content;
    }
  } @else if $point == "large" {
    @media (min-width: 992px) and (max-width: 1199px) {
      @content;
    }
  } @else if $point == "max-large" {
    @media (max-width: 1199px) {
      @content;
    }
  } @else if $point == "min-xlarge" {
    @media (min-width: 1200px) {
      @content;
    }
  } @else if $point == "xlarge" {
    @media (min-width: 1200px) and (max-width: 1399px) {
      @content;
    }
  } @else if $point == "max-xlarge" {
    @media (max-width: 1399px) {
      @content;
    }
  } @else if $point == "xxlarge" {
    @media (min-width: 1400px) {
      @content;
    }
  }
}

@mixin bgHeadings($w, $t) {
  padding: 3rem;
  position: relative;
  &::before {
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: $w;
    z-index: -50;
    background: {
      image: url("../assets/images/contact/background-01.webp");
      repeat: no-repeat;
      size: contain;
    }

    @if $t {
      transform: $t;
    }
  }
}
